"use client";

import { inputs } from "@/constants";
import Input from "./input";
import { FormEvent } from "react";
import { createProduct } from "@/utils/api";
import { Product } from "@/types";
import { useRouter } from "next/navigation";
import { toast } from "react-toastify";

const Form = () => {
    const router = useRouter();
    // form gönderilince
const handleSubmit = (e:FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    // formdata örneği al
    const formData = new FormData(e.target as HTMLFormElement);
    const productData = Object.fromEntries(formData.entries());

    // resim ekle
    const id = Math.round(Math.random() * 100);
    productData.images=`https://picsum.photos/seed/${id}/500/500`;

    // varsayılan rating ve yorum sayısını belirle
    //@ts-ignore
    productData.rating = 0;
    //@ts-ignore
    productData.reviews_count = 0;

    //api'a ekleme isteği at
    createProduct(productData as unknown as Product)
    .then(() => {
        router.push("/products"); 
        router.refresh();
        toast.success("Ürün başarıyla oluştu");
    });
};

  return (
    <form onSubmit={handleSubmit} className="flex flex-col gap-5 mt-5 text-xl">
        {inputs.map((i) => (
            <Input item={i}/>
        ))}
      <div className="flex justify-end gap-8">
        <button type="button" className="bg-gray-300 hover:bg-gray-400 py-2 px-5 rounded-lg text-xl">
            Geri
        </button>
        <button type="submit" className="bg-blue-300 hover:bg-blue-400 py-2 px-5 rounded-lg text-xl">Kaydet</button>
      </div>
    </form>
  )
};

export default Form;
 